<template>
  <div>
    <div class="title">
        组态图
    </div>
    <!-- <el-alert
        title="信息提示"
        type="warning"
        :closable="false">
    </el-alert> -->
    <iframe :src="dashboard" frameborder="0" scrolling="no" width="1200" height="750"></iframe>
  </div>
</template>

<script>
import url from "../../common/api";
export default {
  data() {
    return {
      dashboard: '',
    };
  },
  methods:{
    Fndashboardv2(){
      url.dashboardv2({
        id:this.$route.query.id
      }).then(res=>{
        this.dashboard=res.data.dashboardurl
        console.log(res,'-----------------组态')
        if(res.data.code[0]='200'){
          this.$message({ 
            message: res.data.message[0],
            type: 'success'
          });
        }else{
          this.$message({ 
            message: res.data.message[0],
            type: 'warning' 
          });
        }
      })
    }
  },
  mounted(){
    this.Fndashboardv2()
  }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
    
</style>


